export default {
	data() {
		return {
			user_token: '',
			TabCur: -1,
			scrollLeft: 0,
			MisList: [],
			myfabuInfo: [], //我发布的信息
			iuliInfo: [], //待处理的信息
			yesOrNoName: -2,
			dlaInfo: '', //弹窗信息
			jpuzId: -9 //

		};
	},
	onLoad() {
		let that = this;
		that.user_token = uni.getStorageSync("user_token");
		uni.request({
			url: that.MisUrl + '/jpuz/userGetFaBuMisList',
			method: 'POST',
			header: {
				'user_token': that.user_token
			},
			data: {},
			success: res => {
				that.myfabuInfo = res.data.result
			},
			fail: () => {},
			complete: () => {}
		});
		// uni.request({
		// 	url: that.MisUrl + '/mis/getJpUzNumByJpUzId/1',
		// 	method: 'GET',
		// 	data: {},
		// 	success: res => {},
		// 	fail: () => {},
		// 	complete: () => {}
		// });

	},
	methods: {
		hide() {
			this.yesOrNoName = -2
		},
		//拒绝
		upFalse(e) {
			let that = this;
			that.dlaInfo = "你确定要拒绝吗???????????";
			that.jpuzId = e.currentTarget.dataset.target;
			that.yesOrNoName = e.currentTarget.dataset.target;
		},
		//确定拒绝
		yes() {
			let that = this;
			uni.request({
				url: that.MisUrl + '/uar/userUpMis/-1/' + that.jpuzId,
				method: 'POST',
				header: {
					'user_token': that.user_token
				},
				data: {},
				success: res => {
					uni.showToast({
						title: '已拒绝.'
					});
				},
				fail: () => {},
				complete: () => {}
			});
			this.yesOrNoName = -2
		},
		//同意
		upTrue(e) {
			let that = this;
			that.jpuzId = e.currentTarget.dataset.target;
			uni.request({
				url: that.MisUrl + '/uar/userUpMis/1/' + that.jpuzId,
				method: 'POST',
				header: {
					'user_token': that.user_token
				},
				data: {},
				success: res => {

					uni.showToast({
						title: '已同意.'
					});

					uni.request({
						url: that.MisUrl + '/uar/userUpMisType/' + 0,
						method: 'POST',
						header: {
							'user_token': that.user_token
						},
						data: {},
						success: res => {
							that.iuliInfo = res.data.result
						},
						fail: () => {},
						complete: () => {}
					});

				},
				fail: () => {},
				complete: () => {}
			});
		},
		//跳转到任务详细信息页面
		toUpSquareInfo(e) {
			var item = e.currentTarget.dataset.id;
			uni.setStorage({
				key: 'old_info',
				data: item
			})
			uni.navigateTo({
				url: '../upSquareInfo/upSquareInfo?misId=' + item.misId,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
		tab_02(e) {
			let that = this;
			console.log(e.currentTarget.dataset.id);
			this.TabCur = e.currentTarget.dataset.id;
			var id = that.TabCur
			if (id == -1) {
				uni.request({
					url: that.MisUrl + '/jpuz/userGetFaBuMisList',
					method: 'POST',
					header: {
						'user_token': that.user_token
					},
					data: {},
					success: res => {
						that.myfabuInfo = res.data.result
					},
					fail: () => {},
					complete: () => {}
				});
			} else {
				if (id == 0) {
					uni.request({
						url: that.MisUrl + '/uar/userUpMisType/' + 0,
						method: 'POST',
						header: {
							'user_token': that.user_token
						},
						data: {},
						success: res => {
							that.iuliInfo = res.data.result
						},
						fail: () => {},
						complete: () => {}
					});
				} else {
					uni.request({
						url: that.MisUrl + '/jpuz/userGetFaBuMisList/' + id,
						method: 'POST',
						header: {
							'user_token': that.user_token
						},
						data: {},
						success: res => {
							that.myfabuInfo = res.data.result
						},
						fail: () => {},
						complete: () => {}
					});
				}

			}


		}
	}
}
